@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --bg-body-light: #fff;
  --bg-btn-var1: #9bb537;
  --bg-btn-var2: transparent;
  --color-text-light: #050505;
  --color-text-desc-l: rgba(5, 5, 5, .8);
  --color-categories-text-l: rgba(5, 5, 5, .3);
  --color-input-placeholder-l: rgba(5, 5, 5, .5);
  --color-active-el: #9bb537;
  --color-btn-var1: #f8f8f8;
  --color-btn-var2: #050505;
  --border-input-l: 1px solid rgba(5, 5, 5, .2);
  --border-radius: 15px;
  --border-btn-var2: 1px solid #9bb537;
  --bg-body-dark: #1e1e1e;
  --color-text-d: #fff;
  --color-text-desc-d: rgba(255, 255, 255, .8);
  --color-categories-text-d: rgba(255, 255, 255, .3);
  --color-input-placeholder-d: rgba(255, 255, 255, .5);
  --border-input-d: 1px solid rgba(255, 255, 255, .2);
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  color: var(--color-text-light);
  font-family: Inter, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.btn {
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  display: block;
}

.visibility-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

img {
  line-height: 0;
  display: block;
}

.section {
  padding-bottom: 80px;
}

.container {
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 100px;
  }
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 100px;
  }
}

.dark {
  background-color: var(--bg-body-dark);
  color: var(--color-text-d);
}

.dark .navigation-link, .dark .black-logo, .dark .card-heading, .dark .card-button {
  color: var(--color-text-d);
}

.dark .logo {
  text-shadow: 0 4px 4px rgba(255, 255, 255, .106);
}

.dark label, .dark select, .dark .card-rating, .dark .footer-text {
  color: var(--color-input-placeholder-d);
}

.dark select {
  border: var(--border-input-d);
  background-color: rgba(0, 0, 0, 0);
}

.dark .icon-search {
  fill: var(--color-input-placeholder-d);
}

.dark .input-search {
  color: var(--color-input-placeholder-d);
  border: var(--border-input-d);
  background-color: rgba(0, 0, 0, 0);
}

.dark .card-block:after {
  background: linear-gradient(1deg, #050505 0%, rgba(5, 5, 5, 0) 100%);
}

.dark .card-description {
  color: var(--color-text-desc-d);
}

.dark .mobile-menu .mobile-menu-content, .dark .modal-content {
  background: var(--color-text-light);
}

.switch input {
  display: none;
}

.slider {
  width: 46px;
  height: 20px;
  background-color: #ccc;
  border-radius: 10px;
  transition: background-color .3s;
  display: inline-block;
  position: relative;
}

.slider:before {
  content: "";
  width: 18px;
  height: 18px;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .3s;
  position: absolute;
  top: 50%;
  transform: translate(1px, -50%);
}

input:checked + .slider {
  background-color: #9bb537;
}

input:checked + .slider:before {
  transform: translate(26px, -50%);
}

.btn_scroll {
  opacity: 0;
  z-index: 100;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: var(--bg-btn-var1);
  fill: var(--color-btn-var2);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: opacity .3s ease-in-out;
  animation: slideInDown 6s infinite alternate;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .btn_scroll {
    width: 45px;
    height: 45px;
  }
}

@media screen and (min-width: 1280px) {
  .btn_scroll {
    width: 50px;
    height: 50px;
  }
}

@media screen and (min-width: 768px) {
  .scroll-button-svg {
    width: 15px;
    height: 15px;
  }
}

@media screen and (min-width: 1280px) {
  .scroll-button-svg {
    width: 20px;
    height: 20px;
  }
}

@keyframes slideInDown {
  0% {
    transform: translate3d(0, -100%, 0);
  }

  20% {
    transform: translate3d(0, 0, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

.slideOutUp {
  animation-name: slideOutUp;
}

.open-modal-button {
  background: none;
  border: none;
  align-items: center;
  padding: 0;
  display: flex;
}

.create-order-link {
  align-items: center;
  display: flex;
}

.logo, .navigation-link, .create-order-link, .switch, .open-modal-button {
  padding-top: 20px;
}

@media screen and (min-width: 768px) {
  .logo, .navigation-link, .create-order-link, .switch, .open-modal-button {
    padding-top: 28px;
  }
}

@media screen and (min-width: 1280px) {
  .logo, .navigation-link, .create-order-link, .switch, .open-modal-button {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.header-navigation-list li {
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .header-scroll {
    background-color: beige;
    transition: background-color 2.5s;
  }

  #header {
    width: 100%;
    z-index: 999;
    position: fixed;
    top: 0;
  }
}

.navigation-link {
  font-family: Inter;
  font-size: 12px;
}

.mobile-menu .header-navigation-list li:first-child a {
  color: var(--bg-body-light);
}

.mobile-menu .header-navigation-list li:nth-child(2) a {
  color: var(--color-btn-var1);
}

.svg-list {
  gap: 14px;
  margin-left: auto;
  display: flex;
}

.svg-list label {
  display: none;
}

.navigation-link.current-link {
  color: var(--color-active-el);
}

.switch {
  vertical-align: middle;
  cursor: pointer;
  display: inline-block;
}

.header-navigation-list {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.header-wrapper {
  display: flex;
}

.header-wrapper .header-navigation-list {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-wrapper {
    justify-content: space-between;
  }

  .header-wrapper .header-navigation-list {
    flex-direction: row;
    display: flex;
  }

  .header-wrapper .svg-list {
    align-items: center;
  }
}

.logo {
  color: var(--bg-btn-var1);
  letter-spacing: -.64px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, .25);
  align-items: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .logo {
    letter-spacing: -.72px;
    font-size: 18px;
    line-height: 1.1;
  }
}

@media screen and (min-width: 768px) {
  .svg-list {
    margin-left: 0;
  }

  .svg-list label {
    display: flex;
  }

  .open-modal-button {
    display: none;
  }

  .navigation-link {
    color: var(--color-btn-var2);
  }
}

.black-logo {
  color: var(--color-text-light);
}

.mobile-menu {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translate(100%);
}

.mobile-menu.is-visible {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  z-index: 100;
  transform: translate(0);
}

.mobile-menu .mobile-menu-content {
  background-color: var(--bg-btn-var1);
  width: 52vw;
  height: 100vh;
  padding: 20px;
  position: absolute;
  top: 0;
  right: 0;
}

.mobile-menu .mobile-menu-content .switch {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.mobile-menu .close-modal-button {
  margin-left: auto;
  display: flex;
}

.mobile-menu .header-navigation {
  text-align: center;
  margin-top: 200px;
}

.footer {
  background: var(--bg-body-dark);
  color: var(--color-text-d);
  padding: 20px 0;
}

.footer__wrap {
  flex-direction: column;
  display: flex;
}

.footer-top-line {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.footer .navigation {
  justify-content: space-between;
  align-items: center;
  gap: 60px;
  display: flex;
}

.footer .navigation .logo {
  padding: 10px 0;
}

.footer .navigation .black-logo {
  color: var(--color-text-d);
}

.footer .header-navigation-list {
  gap: 0;
  display: flex;
}

.footer .header-navigation-list .navigation-link {
  color: inherit;
  padding: 10px 0;
}

.footer .svg-list {
  margin-left: auto;
}

.footer .open-modal-button {
  display: none;
}

.footer .create-order-link svg {
  stroke: var(--color-text-d);
}

.footer .switch {
  display: block;
}

.footer-center-line {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.team {
  margin-bottom: 20px;
  position: relative;
}

.team .teame-title {
  text-align: center;
  margin-bottom: 8px;
}

.team .team-list {
  max-width: 200px;
  flex-wrap: wrap;
  gap: 4px;
  display: flex;
}

.team .team-item {
  flex-basis: calc(16.6667% - 3.33333px);
  justify-content: center;
  align-items: center;
  display: flex;
}

.team .team-item img {
  cursor: pointer;
  border-radius: 100%;
}

.support-text {
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 500;
}

.support-link {
  width: 160px;
  height: 44px;
  color: inherit;
  border: var(--border-btn-var2);
  border-radius: var(--border-radius);
  transition: color var(--transition);
  font-size: 18px;
  font-weight: 600;
  line-height: 1.4;
  display: block;
  position: relative;
  overflow: hidden;
  box-shadow: 4px 8px 19px -3px rgba(0, 0, 0, .27);
}

.support-link span {
  white-space: nowrap;
  z-index: 2;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.support-link:before {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: var(--border-radius);
  background-image: url("camouflage-pattern.5be4b53b.png");
  background-position: 0;
  transition: transform .25s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  box-shadow: 4px 8px 19px -3px rgba(0, 0, 0, .27);
}

.support-link:is(:hover, :focus):before {
  transform: translateX(0);
}

.footer-text {
  color: inherit;
  font-size: 12px;
}

.footer-bottom-line {
  margin: 0 auto;
}

.footer-copyright {
  margin-bottom: 10px;
}

.footer-policy-list {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.footer-policy-list a {
  color: inherit;
}

.modal-team-devel {
  background-color: var(--bg-body-light);
  border-radius: var(--border-radius);
  width: 260px;
  z-index: 2;
  padding: 16px;
  position: absolute;
  top: -200%;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: inset 0 0 10px #1f1f1f;
}

.modal-team-devel.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.close-modal-team {
  background: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-team-title {
  color: var(--color-text-light);
  margin-bottom: 8px;
  font-size: 16px;
}

.devel-info {
  gap: 12px;
  display: flex;
}

.devel-info img {
  border-radius: 100%;
}

.info-text .devel-name {
  color: var(--color-text-light);
  margin-bottom: 4px;
  font-size: 18px;
}

.info-text .devel-role {
  color: var(--color-text-light);
  margin-bottom: 4px;
  font-size: 16px;
}

.info-text .devel-responsibility {
  color: var(--color-text-light);
}

@media screen and (min-width: 768px) {
  .footer-center-line {
    flex-direction: row;
    justify-content: space-between;
  }

  .team {
    margin-bottom: 0;
  }

  .team .team-list {
    max-width: none;
    flex-wrap: nowrap;
  }

  .support {
    margin-left: auto;
  }

  .modal-team-devel {
    width: 330px;
    top: -300%;
    left: 0;
    transform: translateX(0);
  }

  .close-modal-team {
    top: 14px;
    right: 14px;
  }

  .modal-team-title {
    font-size: 18px;
  }

  .info-text .devel-name {
    margin-bottom: 8px;
    font-size: 20px;
  }

  .info-text .devel-role {
    margin-bottom: 4px;
    font-size: 18px;
  }
}

.container-all-categories {
  flex-direction: column;
  margin: 0 64px 64px 0;
  display: flex;
}

.all-categories-btn {
  width: 174px;
  height: 50px;
  letter-spacing: -.02em;
  color: var(--color-input-placeholder-l);
  border: var(--border-input-l);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 15px;
  padding: 14px 28px;
  font-family: Inter, sans-serif;
  font-size: 18px;
}

.list-category-btns {
  width: 326px;
  height: 198px;
  flex-direction: column;
  margin-top: 24px;
  display: flex;
  overflow-y: auto;
}

.list-category-btns .item-category-btn {
  width: auto;
  color: var(--color-categories-text-l);
  text-align: left;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-bottom: 18px;
  padding: 0;
  font-family: Inter, sans-serif;
  font-size: 14px;
  line-height: 1.25;
  display: inline-block;
}

.list-category-btns .item-category-active-btn {
  color: var(--bg-btn-var1);
}

@media screen and (min-width: 768px) {
  .list-category-btns {
    width: 176px;
    height: 416px;
    margin-top: 30px;
  }

  .list-category-btns .item-category-btn {
    margin-bottom: 24px;
    font-size: 16px;
  }
}

@media screen and (min-width: 1440px) {
  .list-category-btns {
    width: 235px;
    height: 416px;
    margin-top: 42px;
  }

  .list-category-btns .item-category-btn {
    margin-bottom: 24px;
    font-size: 16px;
    line-height: 1.25;
  }
}

.popular-recipes-container {
  width: 235px;
}

.popular-recipe-title {
  text-transform: uppercase;
  color: var(--color-text-light);
  padding-bottom: 32px;
}

.dark .popular-recipe-title {
  color: var(--color-text-d);
}

.popular-recipe-list:not(:last-child) {
  margin-bottom: 32px;
}

.popular-recipe-card {
  display: flex;
}

.popular-card-image {
  width: 68px;
  height: 68px;
  border-radius: 8px;
  margin-right: 16px;
}

.popular-card-heading {
  color: var(--color-text-light);
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 18px;
}

.dark .popular-card-heading {
  color: var(--color-text-d);
}

.popular-card-description {
  color: var(--color-text-desc-l);
  align-self: flex-end;
  font-size: 10px;
  line-height: 14px;
}

.dark .popular-card-description {
  color: var(--color-text-desc-d);
}

@media screen and (max-width: 768px) {
  .popular-recipes-container {
    width: 332px;
    margin: auto;
  }
}

.filter-form {
  flex-wrap: wrap;
  gap: 14px;
  display: flex;
}

.wrap-form-item {
  flex-flow: column wrap;
  display: flex;
}

.input-wrap {
  border-radius: 15px;
  position: relative;
}

.label-input {
  letter-spacing: .04em;
  color: rgba(5, 5, 5, .5);
  margin-bottom: 10px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3;
  display: block;
}

.icon-search {
  fill: rgba(5, 5, 5, .5);
  pointer-events: none;
  position: absolute;
  bottom: 14px;
  left: 18px;
}

.input-search {
  width: 322px;
  box-sizing: border-box;
  color: rgba(5, 5, 5, .5);
  width: 100%;
  height: 46px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  outline: none;
  padding: 0 0 0 48px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.3;
}

.input-search:-webkit-any(:hover, :focus) {
  border: 2px solid #9bb537;
}

.input-search:is(:hover, :focus) {
  border: 2px solid #9bb537;
}

.label-selector {
  color: rgba(5, 5, 5, .5);
  margin-bottom: 10px;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
  display: block;
}

.time-selector, .area-selector {
  width: 160px;
}

.ingredients-selector {
  width: 188px;
}

.time-selector, .area-selector, .ingredients-selector {
  height: 46px;
  color: rgba(5, 5, 5, .5);
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  outline: none;
  padding-left: 18px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
}

.time-selector:-webkit-any(:hover, :focus) {
  border: 2px solid #9bb537;
}

.time-selector:is(:hover, :focus) {
  border: 2px solid #9bb537;
}

.area-selector:-webkit-any(:hover, :focus) {
  border: 2px solid #9bb537;
}

.area-selector:is(:hover, :focus) {
  border: 2px solid #9bb537;
}

.ingredients-selector:-webkit-any(:hover, :focus) {
  border: 2px solid #9bb537;
}

.ingredients-selector:is(:hover, :focus) {
  border: 2px solid #9bb537;
}

